@font-face {
  font-family: 'Josefin Sans';
  src: url('../../../fonts/JosefinSans-Regular.ttf');
}

.memberContainer {
  position: relative;
  width: 60%;
  height: auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  margin: 25px auto 0 auto;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  font-family: 'Josefin Sans', sans-serif;
  font-size: 2.5em;
  margin: 20px 0;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;

  &::after {
    content: '';
    background-color: #000;
    width: 40%;
    height: 2px;
    position: relative;
    top: 5px;
  }
}
